.loginbackground {
  background-image: url("../images/loginback.png");
  height: 100vh;
  background-size: 100% 100%;
  /* background: #492448 !important; */
  /* min-height: 100%; */
}

.login-screen {
  margin-left: 22vw;
  background-color: #492448 !important;
}

.signup-box {
  top: 50%; /* IMPORTANT */
  left: 50%; /* IMPORTANT */
  display: block;
  position: absolute;
  z-index: 2;
  margin-top: -420px; /* HALF OF THE HEIGHT */
  margin-left: -250px; /* HALF OF THE WIDTH */
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}
.signuptopbar {
  height: 70px;
  width: 500px;
  background-color: #202020;
}

.signup-form-box {
  width: 500px;
  height: 770px;
  background-color: #f4f5f7;
  /* display: block; */
  float: left;
  margin-left: 0%;
  margin-top: 0%;
}

.signup-content {
  margin-top: 15px;
}

.login-form-box {
  width: 500px;
  height: 700px;
  background-color: #f4f5f7;
  /* display: block; */
  float: left;
  margin-left: 5%;
  margin-top: 7%;

  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.login-desc-box {
  width: 500px;
  height: 700px;
  background-color: #202020;

  /* display: block; */
  float: left;
  text-align: center;
  margin-top: 7%;

  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.desc-header {
  /* appearance */
  font-family: descFont;
  font-size: 2.5em;
  color: #f4f5f7;
  margin-top: 47px;
}

.login-header {
  /* appearance */
  font-family: myFirstFont;
  font-size: 3.2em;
  color: #202020;
}

@font-face {
  font-family: myFirstFont;
  src: url("../fonts/DelaGothicOne-Regular.ttf") format("truetype");
}

@font-face {
  font-family: descFont;
  src: url("../fonts/DelaGothicOne-Regular.ttf") format("truetype");
}

.login-form-boxes {
  margin-top: 22%;
}

.signup-form-boxes {
  margin-top: 3%;
}

.login-button {
  margin-top: 40px;

  background-color: #723c70 !important;
  border: #723c70;
}

.login-button:hover {
  background-color: #883985 !important;
}

.signup-link:hover {
  color: #883985 !important;
}

.mycheckbox:focus {
  border-color: #b34caf !important;
}

.mycheckbox:checked {
  background-color: #5f1d5d !important;
  border-color: #b34caf !important;
}

.jete_logo-icon-login {
  height: 100px;
  width: 200px;

  /* min-width: 6%; */
  /* max-width: 7%; */

  /* margin-left: 70px; */
