  position: relative;
  z-index: 50;
  margin-top: 15px;
  margin-left: 180px;
}

.signupimage {
  position: relative;
}

.partnerssignup {
  height: 200px;
  width: 288px;
  margin-top: 100px;
  position: absolute;
  z-index: 0;
  margin-top: 74vh;
